.logo {
    max-width: 5%;
}

.frequency-waveform-channels-AMFM {
    display: flex;
    padding-top: 20px;
    height: 62%;
}

.channel-div-AMFM {
    width: 22%;
    display: flex;
    justify-content: center;
}

.edit-button-contents-AMFM {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.edit-img-AMFM {
    width: 80%;
    margin-top: 1vw;
}

.edit-button-L-AMFM {
    width: 75%;
    height: 60%;
    margin-top: 2vw;
    margin-left: 20%;
    text-transform: capitalize;
    letter-spacing: -0.03em;
    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
    border-radius: 10px;
}

.edit-button-R-AMFM {
    width: 75%;
    height: 60%;
    margin-top: 2vw;
    margin-right: 20%;
    text-transform: capitalize;
    letter-spacing: -0.03em;
    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
    border-radius: 10px;
}

.AMFM-toggle-div {
    display: flex;
    padding-right: 5%;
    width: 33%;
    margin-top: 10px;
}

.modulation-button-AMFM {
    height: 70%;
    width: 45%;
    margin: 0 5px;
    font-size: x-large;
    text-transform: capitalize;
    letter-spacing: -0.03em;
    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
    border-radius: 10px;
}

.frequency-and-waveform-AMFM {
    width: 56%;
}

.frequency-button-div-AMFM {
    display: flex;
    justify-content: center;
    height: 25%;
    margin-bottom: 1vw;
}

.frequency-button-AMFM {
    font-size: x-large;
    height: 100%;
    width: 90%;
    text-transform: capitalize;
    letter-spacing: -0.03em;
    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
    border-radius: 10px;
}

.range-frequency-AMFM {
    --bar-background-active : #f5f1e7;
    --bar-background : #808080;
    --bar-height : 25px;
    --knob-size :30px;
    --knob-border-radius : 4px;
    --knob-background : #d00000;
    --knob-box-shadow: 0px 0px 0px 4px #f5f1e7;
    padding: 0px 20px;
    padding-bottom: 10px;
}

.message-frequency-button-div-AMFM {
    display: flex;
    justify-content: center;
    height: 25%;
    margin-bottom: 1vw;
}

.message-frequency-button-AMFM {
    font-size: x-large;
    height: 100%;
    width: 90%;
    text-transform: capitalize;
    letter-spacing: -0.03em;
    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
    border-radius: 10px;
}

.range-message-frequency-AMFM {
    --bar-background-active : #f5f1e7;
    --bar-background : #808080;
    --bar-height : 25px;
    --knob-size :30px;
    --knob-border-radius : 4px;
    --knob-background : #d00000;
    --knob-box-shadow: 0px 0px 0px 4px #f5f1e7;
    padding: 0px 20px;
    padding-bottom: 10px;
}

.amplitude-start-phase-AMFM {
    display: flex;
    height: 32%;
    margin-top: 2%;
    padding-bottom: 10px;
}

.amplitude-components2-AMFM {
    display: flex;
    flex-direction: column;
    padding-left: 5%;
    width: 33%;
}

.amplitude-button-AMFM {
    font-size: x-large;
    margin-left: 5%;
    margin-right: 5%;
    max-height: 40%;
    text-transform: capitalize;
    letter-spacing: -0.03em;
    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
    border-radius: 10px;
}

.range-amplitude-AMFM {
    --bar-background-active : #f5f1e7;
    --bar-background : #808080;
    --bar-height : 25px;
    --knob-size :30px;
    --knob-border-radius : 4px;
    --knob-background : #d00000;
    --knob-box-shadow: 0px 0px 0px 4px #f5f1e7;
    padding: 0px 14px;
}

.start-buttons-AMFM {
    display: flex;
    justify-content: center;
    margin-top: 10px;
    width: 33%;
}

.hold-button-AMFM {
    font-size: large;
    height: 70%;
    margin: 0 5px;
    text-transform: capitalize;
    letter-spacing: -0.03em;
    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
    border-radius: 10px;
}

.play-pause-img-AMFM {
    width: 50%;
    margin-top: 4px;
}

ion-popover.input-popover-AMFM {
    --width: 45%;
    --background: #f5f1e7;
    --backdrop-opacity: var(--ion-backdrop-opacity, 0.7);
    top: -12%;
    left: -2.5%;
}

ion-popover.input-popover-AMFM-web {
    --width: 45%;
    --background: #f5f1e7;
    --backdrop-opacity: var(--ion-backdrop-opacity, 0.7);
    left: -25%;
    top: -47.5%;
}

.popover-div-AMFM {
    display: flex;
    margin: 10px 20px;
}

.input-popover-text-AMFM {
    font-size: x-large;
    color: #000000;
}

